import React, { useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import * as Yup from 'yup';
import { useSelector, useDispatch } from 'react-redux';
import Link from 'components/Link';
import Button from 'components/Button';
import Field from 'components/Form/Field';
import LogoImage from 'assets/img/rioarte.png';
import { IState } from '../../../store';
import { signInRequest } from '../states/auth/actions';
import {
  Container,
  Content,
  Logo,
  FormElem,
  Background,
  ContainerTitle,
  Title,
  SubTitle,
  FormActions,
} from './styles';
import { User } from '../model/user';
import { IAuthState } from '../states/auth/types';

const SignInSchema = Yup.object().shape({
  user: Yup.string().email('E-mail inválido').required('Obrigatório'),
  password: Yup.string()
    .min(2, 'Obrigatório')
    .max(50, 'Muito grande')
    .required('Obrigatório'),
});

const SignIn: React.FC = () => {
  const history = useHistory();
  const dispatch = useDispatch();
  const authState = useSelector<IState, IAuthState>(state => {
    return state.auth;
  });

  useEffect(() => {
    if (authState.signed) {
      history.push('/dashboard');
    }
  }, [authState.signed, history]);

  const handleSubmit = (formData: any) => {
    const user = new User();
    user.email = formData.user;
    user.password = formData.password;
    dispatch(signInRequest(user));
  };

  return (
    <Container>
      <Content>
        <div>
          <Logo src={LogoImage} alt="RioArte" />
          <ContainerTitle>
            <Title>Olá, tudo bem?</Title>
            <SubTitle>
              Informe no formulário abaixo seus dados de acesso
            </SubTitle>
          </ContainerTitle>

          <FormElem onSubmit={handleSubmit} validationSchema={SignInSchema}>
            <Field
              id="user"
              name="user"
              label="Usuário"
              appearance="outline"
              placeholder="Nome do usuário"
            />

            <Field
              id="password"
              name="password"
              label="Senha"
              appearance="outline"
              placeholder="Senha secreta"
              type="password"
            />

            <FormActions>
              <Link href="/recuperar-senha">Lembrar senha</Link>

              <Button type="submit" text="Acessar" size="md" />
            </FormActions>
          </FormElem>
        </div>
      </Content>

      <Background />
    </Container>
  );
};

export default SignIn;
